.form {
    width: 310px;
  }
  
  .form__title {
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    margin-left: 2px;
  }
  
  .form__row {
    width: 100%;
    margin-bottom: 10px;
  }
  
  .form__row:last-child {
    margin-bottom: 0;
  }
  
  .form__input {
    padding: 10px 10px;
    font-size: 14px;
    font-weight: 400;
    width: 100%;
    box-sizing: border-box;
    border-radius: 3px;
    border: 1px solid #CFCFCF;
  }
  
  .form__input:focus {
    outline: none;
    border: 1px solid #1371C8;
  }
  
  .form__input::placeholder {
    color: #CFCFCF;
  }
  
  .form__input--textarea {
    height: 98px;
    resize: none;
    font-family: unset;
    font-size: 14px;
  }